:root {
  --color-dark: #000;
  --color-white: #fff;
  --color-btn-bg: linear-gradient(91.83deg, #095092 0%, #00a7e5 100%);
  --color-blue: #0578B9;
  --color-light-blue: #00a7e5;
  --color-lighter-blue: #D1EBFF;
  --color-card-grey: #CDCDD1;
  --color-bg: #DDDDE2;
  --border-color: #E1E1E1;
  --color-blue-secondary: #095092;
  --color-grey: #AAAAAD;
  --color-grey2: #CDCDD1;
  --color-grey3: #DDDDE2;
  --main-ff: "Inter", sans-serif;
  --second-ff: "RobotoMono", sans-serif;
  --regular: 400;
  --bold: 700;
  --title-hero: var(--regular) 7.6rem/100% var(--second-ff);
  --title-XL: var(--bold) 6rem/100% var(--second-ff);
  --title-L: var(--regular) 4rem/115% var(--second-ff);
  --title-L-bold: var(--bold) 4rem/110% var(--second-ff);
  --title-M: var(--regular) 3rem/120% var(--second-ff);
  --title-M-bold: var(--bold) 3rem/120% var(--second-ff);
  --title-S: var(--regular) 2rem/120% var(--second-ff);
  --title-S-bold: var(--bold) 2rem/120% var(--second-ff);
  --title-XS: var(--regular) 1.6rem/120% var(--second-ff);
  --title-XS-bold: var(--bold) 1.6rem/120% var(--second-ff);
  --title-XXS: var(--regular) 1.2rem/120% var(--second-ff);
  --title-mobile: var(--bold) 2.4rem/2.7rem var(--second-ff);
  --body-XL: var(--regular) 2rem/125% var(--main-ff);
  --body-XL-bold: var(--bold) 2rem/125% var(--main-ff);
  --body-L: var(--regular) 1.6rem/120% var(--main-ff);
  --body-M: var(--regular) 1.4rem/120% var(--main-ff);
  --body-M-semibold: 600 1.4rem/120% var(--main-ff);
  --body-S: var(--regular) 1.2rem/1.5rem var(--main-ff);
}

@font-face {
  font-weight: 400;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}

@font-face {
  font-weight: 400;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Regular.woff2") format("woff2"), url("../fonts/RobotoMono-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Medium.woff2") format("woff2"), url("../fonts/RobotoMono-Medium.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Bold.woff2") format("woff2"), url("../fonts/RobotoMono-Bold.woff") format("woff");
}

.button-reset {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border-radius: 0;
  border: none;
  font-family: var(--main-ff);
}

.logo {
  width: 20rem;
}

@media screen and (max-width: 767px) {
  .logo {
    width: 14.3rem;
  }
}

.nav ul {
  display: flex;
  align-items: center;
}

.nav ul li {
  margin-right: 2.5rem;
}

.nav ul li:last-child {
  margin-right: 0;
}

.nav ul li a {
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  transition: color 200ms;
}

.nav ul li a:hover {
  color: var(--color-light-blue);
}

.nav ul li.current-menu-item {
  position: relative;
}

.nav ul li.current-menu-item::before {
  position: absolute;
  bottom: calc(100% + 0.5rem);
  left: 50%;
  transform: translateX(-50%);
  width: 0.6rem;
  height: 0.6rem;
  background: linear-gradient(91.83deg, #095092 0%, #00A7E5 100%);
  border-radius: 50%;
  content: "";
}

.footer-nav ul li {
  margin-bottom: 4.5rem;
}

@media screen and (max-width: 767px) {
  .footer-nav ul li {
    margin-bottom: 3rem;
  }
}

.footer-nav ul li:last-child {
  margin-bottom: 0;
}

.footer-nav ul li a {
  font-weight: 500;
  font-size: 2.2rem;
  line-height: 3rem;
  font-family: var(--second-ff);
  transition: color 200ms;
}

.footer-nav ul li a:hover {
  color: var(--color-light-blue);
}

@media screen and (max-width: 767px) {
  .footer-nav ul li a {
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
}

@media screen and (max-width: 991px) {
  .footer-nav ul {
    display: flex;
    flex-wrap: wrap;
  }
  .footer-nav ul li {
    width: 50%;
    margin-bottom: 3rem;
  }
}

.second-font {
  font-weight: 500;
  font-family: var(--second-ff);
}

.second-title {
  font-family: var(--second-ff);
  font-size: 4rem;
  line-height: 4.5rem;
}

.button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 24rem;
  background: var(--color-btn-bg);
  color: var(--color-white);
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  border-radius: 1rem;
  cursor: pointer;
  overflow: hidden;
}

.button span {
  display: flex;
  align-items: center;
  color: transparent;
  position: relative;
  line-height: 1;
  padding: 1.9rem 0 1.9rem 1.5rem;
  flex-shrink: 0;
}

.button span::before {
  content: attr(data-text);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  position: absolute;
  left: 1rem;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all .3s ease;
  transform: translateY(0) translateZ(0);
}

.button span::after {
  content: attr(data-text);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  position: absolute;
  left: 1rem;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all .3s ease;
  transform: translateY(0) translateZ(0);
}

.button span::after {
  top: 100%;
  opacity: 0;
}

.button__icon {
  fill: var(--color-white);
  margin-right: 1.5rem;
  transition: transform 200ms;
}

.button_white {
  background: var(--color-white);
}

.button_white span::before, .button_white span::after {
  color: var(--color-blue);
}

.button_white .button__icon {
  fill: var(--color-blue);
}

.button_white-black {
  background: var(--color-white);
}

.button_white-black span::before, .button_white-black span::after {
  color: var(--color-dark);
}

.button_white-black .button__icon {
  fill: var(--color-dark);
}

.button:hover span::before {
  opacity: 0;
}

.button:hover span::after {
  opacity: 1;
}

.button:hover span::before, .button:hover span::after {
  transform: translateY(-100%) translateZ(0);
}

.button:hover .button__icon-plus {
  transform: rotate(-90deg);
}

.form {
  width: 100%;
}

.form__heading {
  display: block;
  margin-bottom: 3rem;
  font-size: 2rem;
  line-height: 4.5rem;
}

.form__input-container {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 2rem;
}

.form__input-container ul {
  position: absolute;
  bottom: 0.2rem;
  right: 0.5rem;
  font-weight: 500;
  font-size: 1.2rem;
  color: red;
}

.form__input-container._mb10 {
  margin-bottom: 1rem;
}

.form__input {
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--border-color);
  border-radius: 1rem;
  padding: 1.7rem 1.7rem 1.8rem;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  outline: none;
}

.form__input::placeholder {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  color: var(--color-dark);
}

.form__input.parsley-error {
  border-color: red !important;
}

.form__textarea {
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--border-color);
  border-radius: 1rem;
  padding: 1.7rem 1.7rem 1.8rem;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  min-height: 10.6rem;
  resize: none;
  outline: none;
}

.form__textarea::placeholder {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  color: var(--color-dark);
}

.form__button-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form__button-wrapper p {
  max-width: 15.5rem;
  font-size: 1.2rem;
  line-height: 1.4rem;
  color: rgba(111, 111, 111, 0.7);
}

.form__button-wrapper p a {
  text-decoration: underline;
}

.form__button-wrapper p a:hover {
  text-decoration: none;
}

.form__button {
  font-weight: 600;
  min-width: 14.9rem;
}

.form__button._width {
  min-width: 17.2rem;
}

.form._white .form__input {
  border-color: var(--color-white);
  color: var(--color-white);
}

.form._white .form__input::placeholder {
  color: var(--color-white);
}

.form._white .header__mobile-btn span {
  background-color: var(--color-white);
}

.form._white .form__textarea {
  border-color: var(--color-white);
  color: var(--color-white);
}

.form._white .form__textarea::placeholder {
  color: var(--color-white);
}

.form._white .form__button-wrapper p {
  color: var(--color-white);
  max-width: 17.5rem;
}

.form._white .form__button {
  background: var(--color-white);
  color: var(--color-dark);
}

.form._white .form__button span::before, .form._white .form__button span::after {
  color: var(--color-dark);
}

.form._white .form__button-icon {
  fill: var(--color-dark);
}

.social {
  display: flex;
  align-items: center;
}

.social__item {
  margin-right: 1.5rem;
}

.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  fill: var(--color-dark);
}

.social__link .icon {
  transition: fill 200ms;
}

.social__link:hover .icon {
  fill: var(--color-light-blue);
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background-color: var(--color-bg);
  transform: opacity 200ms, visibillty 200ms;
}

.preloader._is-loaded {
  opacity: 0;
  visibility: hidden;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;
}

@media screen and (max-width: 767px) {
  .grid-container {
    gap: 20px 0;
  }
}

.section-name {
  display: block;
  font: var(--body-XL);
}

@media screen and (max-width: 767px) {
  .section-name {
    font: var(--body-S);
  }
}

.slider-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 13.4rem;
}

.slider-nav._small {
  width: 7.2rem;
}

.slider-nav._small .slider-nav__btn {
  width: 3.2rem;
  height: 3.2rem;
}

.slider-nav._small .slider-nav__btn .slider-nav__btn-icon {
  width: 1.5rem;
  height: 1.5rem;
}

.slider-nav__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 5.6rem;
  height: 5.6rem;
  border-radius: 50%;
  border: 1px solid rgba(45, 48, 64, 0.1);
  cursor: pointer;
  transition: background-color 200ms;
}

.slider-nav__btn:hover {
  background-color: rgba(45, 48, 64, 0.1);
}

.slider-nav__btn:active {
  transform: scale(0.99);
}

.slider-nav__btn.swiper-button-lock {
  display: none;
}

.slider-nav__btn-icon {
  width: 2.5rem;
  height: 2.5rem;
  fill: var(--color-dark);
}

.mark {
  padding: 1.6rem 2.4rem;
  background-color: var(--color-white);
  width: 28.8rem;
}

.mark__heading {
  display: flex;
  align-items: flex-start;
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 1.9rem;
  text-transform: uppercase;
  margin-bottom: 2.4rem;
}

.mark__heading span {
  display: block;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 150%;
  text-transform: none;
  color: #ADADAD;
  padding-left: 0.4rem;
}

.mark__coordinates {
  font: var(--body-M);
  margin-bottom: 2.5rem;
}

.mark__legend {
  display: block;
  font: var(--body-S);
  color: #ADADAD;
  margin-top: 2.5rem;
  margin-bottom: 0.4rem;
}

.mark__link {
  display: inline-block;
  font: var(--body-M);
}

.mark__link:hover {
  text-decoration: underline;
}

.drop-menu-container {
  overflow: hidden;
  max-height: 0;
  transition: max-height 200ms;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  line-height: 1.3;
}

@media screen and (min-width: 2100px) {
  html {
    font-size: 14px;
  }
}

@media screen and (max-width: 1536px) {
  html {
    font-size: 9px;
  }
}

@media screen and (max-width: 1366px) {
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 1280px) {
  html {
    font-size: 7px;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 10px;
  }
}

@media screen and (max-width: 320px) {
  html {
    font-size: 9px;
  }
}

body {
  color: var(--color-dark);
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.9rem;
  font-family: var(--main-ff);
  background-color: var(--color-bg);
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
}

h2,
h3,
h4,
h5,
h6 {
  font-family: var(--second-ff);
}

.list-rest {
  padding: 0;
  margin: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 1504px;
  margin: 0 auto;
  padding: 0 40px;
}

@media screen and (min-width: 2100px) {
  .site-container {
    max-width: 1920px;
  }
}

@media screen and (max-width: 767px) {
  .site-container {
    padding: 0 16px;
  }
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-wrapper.overflow-visible {
  overflow: visible;
}

@media screen and (max-width: 767px) {
  .page-wrapper.overflow-visible {
    overflow: hidden;
  }
}

.page-main {
  flex-grow: 1;
}

.header {
  padding-top: 3rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  --txt-color: var(--color-dark);
}

.header._secondary {
  --txt-color: var(--color-white);
}

.header._secondary .header__logo {
  display: none;
}

.header._secondary .header__logo._mobile {
  display: block;
}

.header._secondary .header__mobile-btn span {
  background-color: var(--color-white);
}

@media screen and (max-width: 767px) {
  .header {
    padding-top: 2rem;
  }
}

.header__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
}

.header_left {
  grid-column: 6 span;
  display: flex;
  align-items: center;
}

.header__logo {
  flex-shrink: 0;
}

.header__logo._mobile {
  display: none;
}

.header_right {
  grid-column: 6 span;
  display: flex;
  align-items: center;
}

.header__button {
  display: inline-block;
  margin-left: auto;
  background: url(../img/white-noise.png), var(--color-btn-bg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: var(--color-white);
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  padding: 0.6rem 1rem;
  border-radius: 2rem;
  cursor: pointer;
  transition: opacity 200ms;
}

.header__button:hover {
  opacity: 0.7;
}

@media screen and (max-width: 1199px) {
  .header__nav {
    display: none;
  }
}

.header__nav ul li a {
  color: var(--txt-color);
}

.header__mobile-btn {
  position: relative;
  display: none;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 4rem;
  height: 4rem;
  margin-right: 2rem;
  flex-shrink: 0;
}

.header__mobile-btn.is-active span {
  background-color: var(--color-dark) !important;
}

.header__mobile-btn.is-active span:nth-child(1) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.header__mobile-btn.is-active span:nth-child(2) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.header__mobile-btn span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 100%;
  height: 0.3rem;
  background-color: var(--color-dark);
  border-radius: 2.4rem;
  transition: all 200ms;
}

.header__mobile-btn span:nth-child(1) {
  top: 40%;
}

.header__mobile-btn span:nth-child(2) {
  top: 60%;
}

.header__mobile-btn-icon {
  width: 2rem;
  height: 2rem;
}

@media screen and (max-width: 1199px) {
  .header__mobile-btn {
    display: flex;
  }
}

.footer__container {
  background-color: var(--color-white);
  padding: 3rem;
  border-radius: 20px 20px 0px 0px;
}

@media screen and (max-width: 767px) {
  .footer__container {
    padding: 3rem 1.5rem 2rem;
  }
}

.footer__top-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  margin-bottom: 13.7rem;
}

@media screen and (max-width: 991px) {
  .footer__top-block {
    margin-bottom: 6rem;
  }
}

.footer__top-block-col:nth-child(1) {
  grid-column: 3 span;
}

.footer__top-block-col:nth-child(2) {
  grid-column: 5 span;
}

.footer__top-block-col:nth-child(3) {
  grid-column: 4 span;
}

@media screen and (max-width: 991px) {
  .footer__top-block-col {
    grid-column: 12 span !important;
    margin-bottom: 6rem;
  }
  .footer__top-block-col:nth-child(1) {
    order: 2;
  }
  .footer__top-block-col:nth-child(2) {
    order: 3;
    margin-bottom: 0;
  }
  .footer__top-block-col:nth-child(3) {
    order: 1;
  }
}

.footer__legend {
  display: block;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #adadad;
  margin-bottom: 0.1rem;
}

.footer__contact-link {
  display: inline-block;
  font-weight: 500;
  font-size: 3.2rem;
  line-height: 4.8rem;
  font-family: var(--second-ff);
  letter-spacing: -0.04em;
}

@media screen and (max-width: 767px) {
  .footer__contact-link {
    font-size: 2rem;
    line-height: 3rem;
  }
}

.footer__contact-link._phone {
  margin-bottom: 4.3rem;
}

@media screen and (max-width: 767px) {
  .footer__contact-link._phone {
    margin-bottom: 2rem;
  }
}

.footer__contact-link._mail {
  margin-bottom: 8.2rem;
  background: var(--color-btn-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media screen and (max-width: 991px) {
  .footer__contact-link._mail {
    margin-bottom: 2rem;
  }
}

.footer__adress {
  display: block;
  font-weight: 400;
  font-size: 2rem;
  line-height: 3rem;
  color: #adadad;
  max-width: 35rem;
}

@media screen and (max-width: 767px) {
  .footer__adress {
    font-size: 1.4rem;
    line-height: 2.1rem;
  }
}

.footer__bottom-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  align-items: end;
}

.footer__bottom-block-col:nth-child(1) {
  grid-column: 8 span;
