  }
}

.solution-content__link {
  flex-shrink: 0;
  font: var(--body-L);
  margin-right: 5.2rem;
  color: var(--color-grey);
  transition: color 200ms;
}

.solution-content__link.is-active {
  color: var(--color-dark);
}

.solution-content__link:last-child {
  margin-right: 0;
}

.solution-content__link:hover {
