  }
}

.solution-content__list {
  display: flex;
  flex-direction: column;
  counter-reset: section;
}

.solution-content__item {
  margin-bottom: 6rem;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  border-radius: 2rem;
  overflow: hidden;
  background-color: var(--color-white);
}

