  }
}

@media screen and (max-width: 767px) {
  .solution-content__item {
    margin-bottom: 2rem;
  }
}

.solution-content__item:last-child {
  margin-bottom: 0;
}

.solution-content__item-left-block {
  grid-column: 5 span;
  padding: 3rem 3rem 5rem 3rem;
}

@media screen and (max-width: 991px) {
