  }
}

.solution-content__item-body ul li::before {
  position: absolute;
  top: 0.7rem;
  left: 0;
  width: 0.4rem;
  height: 0.4rem;
  background-color: var(--color-blue);
  border-radius: 50%;
  content: "";
}

@media screen and (max-width: 767px) {
  .solution-content__item-body ul li::before {
    top: 0.4rem;
  }
}

.solution-content__item-imgs-wrapper {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 24px;
  width: 59.6rem;
}

@media screen and (max-width: 991px) {
  .solution-content__item-imgs-wrapper {
    width: auto;
  }
}

.solution-content__item-imgs-wrapper img {
  grid-column: 5 span;
  border-radius: 2rem;
  filter: drop-shadow(4px 4px 11px rgba(0, 0, 0, 0.05)) drop-shadow(10px 10px 34px rgba(0, 0, 0, 0.25));
  justify-self: center;
}

.solution-content__item-imgs-wrapper img:nth-child(1) {
  grid-column: 10 span;
}

.solution-content__item-imgs-wrapper img:nth-child(4n) {
  grid-column: 10 span;
}

@media screen and (max-width: 991px) {
  .solution-content__item-imgs-wrapper img {
    grid-column: 10 span !important;
  }
}

.solution-content__item-mobile {
  background: var(--color-btn-bg);
  align-items: center;
  justify-content: center;
  padding: 4rem 3.2rem;
  display: none;
}

@media screen and (max-width: 767px) {
  .solution-content__item-mobile {
    display: flex;
  }
}
